import { Component, h, Prop, State } from '@stencil/core';
import { orderSuccess, OrderSuccessData } from "../../../global/data/orderEnd";
import Fetch from '../../../global/functions/fetch';

@Component({
    tag: 'ks-page-order-end',
    styleUrl: 'orderEnd.css'
})
export class PageOrderEnd {
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() orderData: string;
    @State() displayPayment: boolean = true;
    @State() paymentOpacity: boolean = true;

    componentWillLoad() {
        const orderDataElement = document.getElementById(this.orderData);
		const orderData = JSON.parse(orderDataElement.innerHTML) as OrderSuccessData;

        Object.keys(orderData).map(key => {
			orderSuccess.set(key as keyof OrderSuccessData, orderData[key]);
		});

        this.track();


        const verify = orderSuccess.get('verify');
        const order = orderSuccess.get('orderId');

        if(verify) {
            this.displayPayment = false;
            this.paymentOpacity = false;

            this.verify(verify, order);

            setTimeout(()=>{
                this.displayPayment = true;
            }, 3000);

            setTimeout(()=>{
                this.paymentOpacity = true;
            }, 3100);
        }
    }

    verify(verify, order) {
        setTimeout(()=>{
            Fetch(verify + `&order=${order}`)
                .then(response => {
                    response.text()
                        .then(text=>{
                            if(text == "OK")
                                document.location.reload();
                            else this.verify(verify, order);
                        })
                        .catch(()=> this.verify(verify, order))
                })
                .catch(()=> this.verify(verify, order))
        }, 2000);
    }

    track() {
        /*if(!orderSuccess.get('error'))
            window.dataLayer?.push({
                currency: 'PLN',
                transaction_id: orderSuccess.get('orderID'),
                value: orderSuccess.get('orderValue'),
                items: Object.entries(orderSuccess.get('products')).map( ([id, product]) => { return {
                    item_id: id,
                    item_name: product.name,
                    currency: 'PLN',
                    price: product.price,
                    quantity: product.amount
                }}),
            });
        */
    }

	render() {
		return <ks-page-base skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            <div class="card">
                <h1 class="heading">{orderSuccess.get('heading')}</h1>

                <section class="content">
                    <div class="message">{orderSuccess.get('message')}</div>

                    {orderSuccess.get('error') ? [
                        <h2>{orderSuccess.get('errorHeading')}</h2>,
                        <div>{orderSuccess.get('errorMessage')}</div>
                    ] : null}

                    {orderSuccess.get('verify') ?
                        <div class="loader">
                            <ks-loader dark />
                        </div> 
                    : null}

                    <div class="payment" style={{display: this.displayPayment ? null : "none", opacity: this.paymentOpacity ? null : "0"}}>
                        <slot/>
                    </div>

                    <div class="buttons">
                        {orderSuccess.get('orderString') ? 
                            <ks-button round link={orderSuccess.get('orderLink')} name={orderSuccess.get('orderString')} />
                        : null}
                        
                        <ks-button round link="/" name={orderSuccess.get('homepage')} />
                    </div>
                </section>
            </div>
        </ks-page-base>;
	}
}
