ks-page-order-end .card {
    display: block;
    padding: 0px;
    box-sizing: border-box;

    max-width: 800px;
    width: 100%;
    margin: auto;
    padding: 50px 20px;

    color: var(--card-text-color);
    text-align: center;
}

ks-page-order-end .card ks-icon {
    margin-right: 10px;
}

ks-page-order-end .card h1 {
    font-size: 22px;
    font-weight: 700;
}

ks-page-order-end .card .payment {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    opacity: 1;
    transition: opacity ease 0.3s;
}

ks-page-order-end .card .loader {
    position: relative;
    height: 40px;
    margin-top: 20px;
}

ks-page-order-end .card .buttons {
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

ks-page-order-end .card .buttons ks-button {
    margin: 5px;
}


ks-page-order-end .card h2 {
    margin: 20px auto 5px auto;
    font-weight: 700;
}